import React from 'react'

import {
  Box,
  Button,
  Collapse,
  HStack,
  useClipboard,
  useDisclosure,
} from '@chakra-ui/react'

import IconCopy from '@/assets/images/icons/ic-copy.svg'
import IconMail from '@/assets/images/icons/ic-mail.svg'
import IconView from '@/assets/images/icons/ic-view.svg'

import Mail from './Mail'

type Props = {
  number: string
}

const BilletOptions = ({ number }: Props) => {
  const { hasCopied, onCopy } = useClipboard(number)
  const { isOpen, onToggle } = useDisclosure()

  function handleCopyBilletNumber() {
    onCopy()
  }

  function handleDownloadBillet() {
    console.log('handleDownloadBillet')
  }

  function handleOpenEmailBox() {
    onToggle()
  }

  return (
    <Box>
      <HStack justifyContent="space-between" spacing="5" mt="7">
        <Button
          onClick={handleCopyBilletNumber}
          rightIcon={<IconCopy />}
          borderColor="blue.light"
          borderWidth="2px"
          color="blue.light"
          fontWeight="bold"
          fontSize="sm"
          variant="outline"
          flex="1"
          _focus={{ background: 'blue.light', color: 'white' }}
        >
          {hasCopied ? 'Copiado ✓' : 'Copiar Código'}
        </Button>
        <Button
          onClick={handleDownloadBillet}
          rightIcon={<IconView />}
          borderColor="blue.light"
          borderWidth="2px"
          color="blue.light"
          fontWeight="bold"
          fontSize="sm"
          variant="outline"
          flex="1"
          _focus={{ background: 'blue.light', color: 'white' }}
        >
          Visualizar Boleto
        </Button>
        <Button
          onClick={handleOpenEmailBox}
          rightIcon={<IconMail />}
          borderColor="blue.light"
          borderWidth="2px"
          color="blue.light"
          fontWeight="bold"
          fontSize="sm"
          variant="outline"
          flex="1"
          _focus={{ background: 'blue.light', color: 'white' }}
        >
          Enviar por e-mail
        </Button>
      </HStack>

      <Collapse in={isOpen}>
        <Mail />
      </Collapse>
    </Box>
  )
}

export default BilletOptions
