import React, { useState } from 'react'

import { Button, HStack, Input } from '@chakra-ui/react'

const SendEmailComponent = () => {
  const [emailAddress, setEmailAddress] = useState('')

  function sendEmail() {
    console.log('send email to', emailAddress)
  }

  return (
    <HStack justifyContent="space-between" spacing="5" mt="7">
      <Input
        type="email"
        placeholder="Seu e-mail"
        borderColor="gray.regular"
        borderWidth="4"
        fontSize="sm"
        flex="2"
        value={emailAddress}
        onChange={event => setEmailAddress(event.target.value)}
      />
      <Button
        onClick={sendEmail}
        bg="green.regular"
        color="white"
        fontWeight="bold"
        fontSize="sm"
        variant="outline"
        flex="1"
      >
        Confirmar
      </Button>
    </HStack>
  )
}

export default SendEmailComponent
