import React from 'react'

import { Box, Heading, Text, Icon, Button } from '@chakra-ui/react'

import { useAuth } from '@/core/context/hooks/useAuth'

import Billet from '@/assets/images/icons/ic-billet.svg'
import Arrow from '@/assets/images/icons/arrow-to-left.svg'

const Hero = () => {
  const { logout, isLoggedIn } = useAuth()

  const handleGoBack = () => {
    logout()
  }

  return (
    <Box
      as="header"
      bg="blue.light"
      paddingTop="14"
      paddingBottom={{ base: '12', lg: 14 }}
      width="full"
      textAlign="center"
    >
      {isLoggedIn && (
        <Button
          onClick={handleGoBack}
          position="absolute"
          top="10"
          left={{ base: '2', lg: '6' }}
          variant="unstyled"
        >
          <Icon as={Arrow} width={{ base: '3', lg: '4' }} height="auto" />
        </Button>
      )}

      <Icon
        as={Billet}
        width={{ base: '8', lg: '20' }}
        height="auto"
        mb={{ base: '3', lg: '6' }}
      />
      <Heading
        as="h1"
        color="white"
        fontWeight="bold"
        fontSize={{ base: 'xl', lg: '5xl' }}
        mb="2"
        textTransform="uppercase"
      >
        2ª Via da fatura
      </Heading>
      <Text
        color="white"
        fontSize={{ base: 'xs', lg: 'lg' }}
        width={{ base: '48', lg: 'auto' }}
        mx="auto"
        textTransform={{ lg: 'uppercase' }}
      >
        {true
          ? 'Cobranças em aberto'
          : 'Preencha os dados para solicitar a segunda via do seu boleto'}
      </Text>
    </Box>
  )
}

export default Hero
