import React, { Fragment } from 'react'

import { BrowserView, MobileView } from 'react-device-detect'

import { Container, VStack } from '@chakra-ui/react'

import InvoiceCard from './InvoiceCard'
import InvoiceCardMobile from './InvoiceCard/index.mobile'

const ListComponent = () => {
  const invoices = [1, 2]

  return (
    <Container maxWidth={{ base: 'container.sm', lg: 'container.lg' }}>
      <Fragment>
        <MobileView>
          <VStack marginY="8" marginX="4" spacing="4">
            {invoices.map(invoice => (
              <InvoiceCardMobile key={invoice.toString()} />
            ))}
          </VStack>
        </MobileView>

        <BrowserView>
          <VStack marginY="14" spacing="10">
            {invoices.map(invoice => (
              <InvoiceCard key={invoice.toString()} />
            ))}
          </VStack>
        </BrowserView>
      </Fragment>
    </Container>
  )
}

export default ListComponent
