import React from 'react'

import {
  Box,
  Button,
  FormControl,
  FormLabel,
  Input,
  Select,
  VStack,
} from '@chakra-ui/react'

import { useForm, SubmitHandler } from 'react-hook-form'
import InputMask from 'react-input-mask'

import { fetchOpenCharges } from '@/core/services/charges'

import { useAuth } from '@/core/context/hooks/useAuth'

import { Person, getPersonFormLabel } from '@/util/enum/Person'

type FormProps = {
  person: Person
}

interface IFormInput {
  document: string
  type: number
}

const FormComponent = ({ person }: FormProps) => {
  const {
    register,
    handleSubmit,
    formState: { errors, isValidating, isSubmitting },
  } = useForm<IFormInput>()

  const { login } = useAuth()

  const onSubmit: SubmitHandler<IFormInput> = async data => {
    const { document, type } = data

    try {
      // const response = await fetchOpenCharges({ document, id_reason: type })

      // const charges = response?.data?.Data?.Cobrancas

      // TODO: fill the charges

      login()
    } catch (err) {
      console.log({ err })
    }
  }

  return (
    <Box
      as="main"
      marginTop="8"
      width="full"
      borderColor="gray.regular"
      borderWidth="1px"
      paddingLeft="7"
      paddingRight="5"
      paddingY="10"
      rounded="xl"
    >
      <VStack as="form" onSubmit={handleSubmit(onSubmit)} spacing="5">
        <FormControl>
          <FormLabel color="gray.regular" fontSize="sm">
            {getPersonFormLabel(person)}
          </FormLabel>
          <Input
            borderColor="gray.regular"
            color="gray.regular"
            fontSize="sm"
            name="document"
            placeholder="XXX.XXX.XXX-XX"
            {...register('document', { pattern: /\d{3}.\d{3}.\d{3}-\d{2}/g })}
          />
        </FormControl>

        <FormControl>
          <FormLabel color="gray.regular" fontSize="sm">
            Motivo
          </FormLabel>

          <Select
            placeholder="Selecione o motivo da solicitação"
            borderColor="gray.regular"
            color="gray.regular"
            fontSize="sm"
            name="reason"
            {...register('reason')}
          >
            <option value="" hidden>
              Selecione
            </option>
            <option value="990">Recebi após o vencimento</option>
            <option value="991">Perdi ou foi danificado</option>
            <option value="992">Não recebi</option>
            <option value="993">Prefiro retirar pelo site</option>
            <option value="1430">Atualização de boleto vencido</option>
            <option value="1431">Pagar com cartão de crédito</option>
          </Select>
        </FormControl>

        <Button
          type="submit"
          alignSelf="flex-end"
          bg="green.regular"
          color="white"
          fontSize="sm"
          fontWeight="bold"
          height="10"
          textTransform="uppercase"
          rounded="lg"
          width="40"
          isLoading={isSubmitting || isValidating}
        >
          Enviar
        </Button>
      </VStack>
    </Box>
  )
}

export default FormComponent
